@import url(//db.onlinewebfonts.com/c/38d41072aa88a50711d4d50dd0d50f6b?family=Neue+Machina);
@font-face {
  font-family: "neue-machina";
  src: url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.eot");
  src: url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.woff") format("woff"), url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/38d41072aa88a50711d4d50dd0d50f6b.svg#Neue Machina") format("svg"); }

html {
  scroll-behavior: smooth; }

body {
  text-align: left;
  font-family: "neue-haas-unica", sans-serif;
  color: #3e3e3e; }

p {
  font-weight: 450; }

a:hover {
  text-decoration: none; }

.title {
  font-family: "neue-machina", sans-serif;
  font-size: 3.5rem; }

.mono-header {
  font-family: "neue-machina", sans-serif; }

.link {
  font-weight: 500;
  text-decoration: none;
  color: #545454; }

.link:hover {
  color: #4305f2; }

.text-link {
  display: inline-block;
  color: #4305f2;
  font-weight: 600;
  transition: color;
  background-size: 100% 200%;
  background-image: linear-gradient(to top, transparent 0%, transparent 50%, #4305f2 50%, #4305f2 100%);
  background-position: 0 100%;
  will-change: background-position; }

.text-link:hover {
  animation: backgroundAnimateIn 0.3s;
  animation-fill-mode: forwards; }

@keyframes backgroundAnimateIn {
  0% {
    background-position: 0 -100%; }
  63% {
    background-position: 0 -95%; }
  86% {
    background-position: 0 2.5%; }
  100% {
    color: white;
    background-position: 0 0%; } }

.black {
  color: #000000; }

.light {
  font-weight: 300; }

.italic {
  font-style: italic; }

.container-hero {
  padding: 0; }

.hero {
  max-width: 100%;
  padding: 0; }

.arrow {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
  height: 2rem;
  margin: 2rem 0rem;
  cursor: pointer; }

@media screen and (min-width: 576px) {
  .intro {
    margin: 8rem 0 2rem 0; }
  .title {
    font-size: 4rem; }
  .text-section {
    padding: 0; } }

@media screen and (min-width: 768px) {
  .intro {
    margin: 8rem 0 2rem 0; }
  .title {
    font-size: 3rem; } }

@media screen and (min-width: 1300px) {
  .intro {
    margin: 10rem 0 4rem 0; }
  .text-section {
    width: 100%; } }
